.cstm-popup-list {
  display: none;
}

.cstm-popup {
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9999;
  transition: opacity 0.2s 0.25s, visibility 0.2s 25s;
}
.cstm-popup.center .cstm-popup-container {
  align-items: center;
}
.cstm-popup.top .cstm-popup-container {
  align-items: flex-start;
}
.cstm-popup.bottom .cstm-popup-container {
  align-items: flex-end;
}
.cstm-popup.left .cstm-popup-container {
  justify-content: flex-start;
}
.cstm-popup.right .cstm-popup-container {
  justify-content: flex-end;
}
.cstm-popup.visible {
  visibility: visible;
  opacity: 1;
  transition: opacity 0.2s, visibility 0.2s;
}

.cstm-popup-darker {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.75);
}

.cstm-popup-wrapper {
  padding: 20px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
}

.cstm-popup-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100%;
}

.cstm-popup-main {
  opacity: 0;
  position: relative;
  background-color: #fff;
  padding: 20px;
  transition: all 0.25s;
}

.cstm-popup.visible .cstm-popup-main {
  opacity: 1;
  transition: all 0.25s 0.25s;
}

.cstm-popup.center .cstm-popup-wrapper .cstm-popup-main {
  transform: scale(0.5);
}
.cstm-popup.center.visible .cstm-popup-wrapper .cstm-popup-main {
  transform: scale(1);
}
.cstm-popup.top .cstm-popup-wrapper {
  padding-top: 0;
  overflow: hidden;
}
.cstm-popup.top .cstm-popup-wrapper .cstm-popup-main {
  transform: translateY(-100%);
}
.cstm-popup.top.visible .cstm-popup-wrapper .cstm-popup-main {
  transform: translateY(0);
}
.cstm-popup.bottom .cstm-popup-wrapper {
  padding-bottom: 0;
  overflow: hidden;
}
.cstm-popup.bottom .cstm-popup-wrapper .cstm-popup-main {
  transform: translateY(100%);
}
.cstm-popup.bottom.visible .cstm-popup-wrapper .cstm-popup-main {
  transform: translateY(0);
}
.cstm-popup.left .cstm-popup-wrapper {
  padding-left: 0;
  overflow: hidden;
}
.cstm-popup.left .cstm-popup-wrapper .cstm-popup-main {
  transform: translateX(-100%);
}
.cstm-popup.left.visible .cstm-popup-wrapper .cstm-popup-main {
  transform: translateX(0);
}
.cstm-popup.right .cstm-popup-wrapper {
  padding-right: 0;
  overflow: hidden;
}
.cstm-popup.right .cstm-popup-wrapper .cstm-popup-main {
  transform: translateX(100%);
}
.cstm-popup.right.visible .cstm-popup-wrapper .cstm-popup-main {
  transform: translateX(0);
}

.cstm-popup-close {
  position: absolute;
  top: 0;
  right: 0;
}

